<!--
Filename: register.vue 
Use: Allow users to register 
Creator: core
Date: unknown
Dependencies: 
  axios,
  @/i18n/register
-->

<template>
  <div class="register-view ly-nav-margin">

    <div class="container">
      <div class="row">

        <form class="d-flex flex-column align-items-center">

          <div style="height: 2rem"></div>
          <img src="../assets/anmelden.svg" alt="Login" class="w-50">
          <h1 class="text-center">{{ i18n.title }}</h1>

          <!-- name -->
          <div class="ly-input" :class="{error: errName}">
            <input id="register-focus" v-model="name" :placeholder="i18n.namePH"
            type="text" :disabled="submitOk" @blur="isNameTaken" class="w-100"
            aria-describedby="name-err">
          </div>
          <div class="text-center" :class="{'d-none': !errName}">
            <span id="name-err" class="text-center" v-show="nameTaken"
              :aria-hidden="!nameTaken">
              {{ nameTaken ? i18n.nameTaken : i18n.nameErr }}
            </span>
          </div>

          <!-- email -->
          <div class="ly-input" :class="{error: errEmail}">
            <input v-model="email" :placeholder="i18n.emailPH" type="text"
            :disabled="submitOk" @blur="isEmailTaken" class="w-100"
            aria-describedby="email-err">
          </div>
          <div class="text-center" :class="{'d-none': !errEmail}">
            <span id="email-err" class="text-center" v-show="emailTaken"
              :aria-hidden="!emailTaken">
              {{ emailTaken ? i18n.emailTaken : i18n.emailErr }}
            </span>
          </div>

          <!-- password -->
          <div class="ly-input" :class="{error: errPwds}">
            <input v-model="pwd1" :placeholder="i18n.pwd1PH" type="password"
            :disabled="submitOk" class="w-100" aria-describedby="pwd-err">
          </div>
          <div class="ly-input" :class="{error: errPwds}">
            <input v-model="pwd2" :placeholder="i18n.pwd2PH" type="password"
            :disabled="submitOk" class="w-100" aria-describedby="pwd-err">
          </div>
          <div id="pwd-err" class="text-center" :class="{'d-none': !errPwds}"
            :aria-hidden="!errPwds">
            {{ i18n.pwdErr }}
          </div>

          <!-- profile pic -->
          <!--
          <div style="height: 2rem"></div>
          <div class="position-relative">
            <div class="position-absolute">{{ i18n.profilePic }}</div>

            <ly-input-img v-model="profileImg"
                          style="width: 7rem"
                          class="m-auto">
              <img src="../assets/hochladen.svg" alt="Profilbild whlen">
            </ly-input-img>
          </div>
          -->

          <!-- submit -->
          <div style="height: 4rem"></div>
          <h4 :class="{'d-none': busy || submitOk}">
            <button v-if="!errForm"
                    @click.prevent="submit"
                    type="submit"
                    class="btn btn-lg btn-block btn-outline-dark"
                    style="border: 2px solid black">
              {{ i18n.submit }} <i class="fas fa-arrow-right"></i>
            </button>
          </h4>
          <!-- still form errors -->
          <h5 id="form-err" class="text-center" :class="{'d-none': !errForm}">
            {{ i18n.formErr }}
          </h5>
          <!-- busy note -->
          <h5 class="text-center" :class="{'d-none': !busy}">
            {{ i18n.busy }} <i class="fas fa-spinner fa-spin"></i>
          </h5>
          <!-- submit ok: goto login -->
          <h5 class="text-center" :class="{'d-none': !submitOk}">
            <router-link to="/login">
              <div>
                <u>{{ i18n.success }}</u>
              <img src="../assets/fertig.svg" alt="Erfolg" style="width: 3rem">
              </div>
            </router-link>
          </h5>
          <!-- submit not ok -->
          <h5 class="text-center">{{ errmsg }}</h5>

        </form>

      </div>
    </div>

  </div>
</template>

<script>
import http from 'axios'
import * as i18n from '@/i18n/register'

export default {
  name: 'register-view',
  mounted () {
    document.querySelector('#register-focus').focus()
  },
  data () {
    return {

      /* form model */
      name: '',
      email: '',
      pwd1: '',
      pwd2: '',
      profileImg: null,

      /* temp */
      errmsg: '',
      busy: false,
      submitOk: false,
      nameTaken: false,
      emailTaken: false
    }
  },
  computed: {

    /**
     * i18n: Load translation files depending on user language
     * 
     * Author: cmc
     * 
     * Last updated: March 21, 2021
     * 
     */
    i18n() {
      return i18n[this.$store.state.profile.lang]
    },

    /**
     * errName: form validation for name
     * 
     * Author: core
     * 
     * Last Updated: unknown
     */
    errName() {
      return /\W/.test(this.name) || /^$/.test(this.name) ||
        this.nameTaken
    },

    /**
     * errEmail: form validation for email
     * 
     * Author: core
     * 
     * Last Updated: unknown
     */
    errEmail() {
      return !(/^[^@\s]+[@][^@\s]+$/.test(this.email)) ||
        this.emailTaken
    },

    /**
     * errPwds: form validation for password input
     * 
     * Author: core
     * 
     * Last Updated: unknown
     */
    errPwds() {
      return (/^$/.test(this.pwd1) || /^$/.test(this.pwd2)) ||
        (this.pwd1 !== this.pwd2)
    },

    /**
     * errForm: form validation altogether
     * 
     * Author: core
     * 
     * Last Updated: unknown
     */
    errForm() {
      return this.errName || this.errEmail || this.errPwds
    }
  },
  methods: {

    /**
     * function focusImgInput: focus #image-input html element
     * 
     * Author: core
     * 
     * Last Updated: unknown
     */
    focusImgInput() {
      document.querySelector('#image-input').focus()
    },

    /**
     * Function isNameTaken: return if chosen name is already taken
     * 
     * Author: core
     * 
     * Last Updated: unknown
     */
    isNameTaken() {
      let ctx = this
      if (ctx.name.length === 0) {
        ctx.nameTaken = false
        return
      }
      http.get(`accounts/name/${ctx.name}`)
        .then( ({data}) => {
          ctx.nameTaken = data
        })
    },

    /**
     * Function isEmailTake: return if given email is already taken
     * 
     * Author: core
     * 
     * Last Updated: unknown 
     */
    isEmailTaken() {
      let ctx = this
      if (ctx.email.length === 0) {
        ctx.emailTaken = false
        return
      }
      http.get(`accounts/email/${ctx.email}`)
        .then(({data}) => {
          ctx.emailTaken = data
        })
    },

    /**
     * Function submit: collect requests for changes, then shoot them
     * 
     * Author: core
     * 
     * Last Updated: unknown
     */
    submit() {
      if (this.errForm || this.nameTaken || this.emailTaken) {
        console.log('Not Submitting')
        return
      }

      console.log('Submitting...')
      this.busy = true
      let ctx = this

      const avatarFileName = ctx.profileImg
        ? `${ctx.name}.${ctx.profileImg.name.split('.').slice(-1).pop()}` : ''

      let requests = [
        http.post(`accounts/student`, {
          email: ctx.email,
          username: ctx.name,
          password: ctx.pwd1,
          avatar: avatarFileName,
          lang: ctx.$store.state.profile.lang
        })
      ]

      //
      // upload profile pic if set
      if (ctx.profileImg) {
        const data = new FormData()
        data.append('file', ctx.profileImg)
        requests.push(
          http.post(`storage/img/upload?name=${avatarFileName}`, data)
          .then(res => console.log(res))
          .catch(err => console.error(err))
        )
      }

      http.all(requests)
        .then(http.spread( () => {
          ctx.submitOk = true
        }))
        .catch( (err) => {
          console.log(err)
          ctx.errmsg = this.i18n.fail
        })
        .finally( () => {
          ctx.busy = false
        })
    }
  },
  components: {
  }
}
</script>

<style scoped>
*:focus {
  outline-offset: 5px;
  outline: 2px dashed deepskyblue;
}

form {
  width: 21rem;
  margin: auto;
}

form > * {
  width: 90%;
}

a {
  color: black;
}
</style>
